	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"master's student in National Taiwan University, research interests cover Deep Learning, Natural Language Processing, and dialogue systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/IJCNLP2017/index.md absPath of file >>> MarkdownRemark","html":"<p>Cite the paper:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">@inproceedings{chi2017speaker,\n  author    = {Ta-Chung Chi and Po-Chun Chen and Shang-Yu Su and Yun-Nung Chen},\n  title\t    = {Speaker Role Contextual Modeling for Language Understanding and Dialogue Policy Learning},\n  booktitle = {Proceedings of IJCNLP},\n  year\t    = {2017}\n}</code></pre>\n      </div>\n<p>Language understanding (LU) and dialogue policy learning are two essential components in conversational systems.\nHuman-human dialogues are not well-controlled and often random and unpredictable due to their own goals and speaking habits.</p>\n<p>This paper proposes a role-based contextual model to consider different speaker roles independently based on the various speaking patterns in the multi-turn dialogues.\nThe experiments on the benchmark dataset show that the proposed role-based model successfully learns role-specific behavioral patterns for contextual encoding and then significantly improves language understanding and dialogue policy learning tasks (The source code is available at: <a href=\"https://github.com/MiuLab/Spk-Dialogue\">https://github.com/MiuLab/Spk-Dialogue</a>).</p>","fields":{"tagSlugs":["/tags/dialogue/","/tags/nlu/"]},"frontmatter":{"title":"Speaker Role Contextual Modeling for Language Understanding and Dialogue Policy Learning","tags":["Dialogue","NLU"],"date":"2017-08-19T22:40:32.169Z","description":"Ta-Chung Chi*, Po-Chun Chen*, Shang-Yu Su*, and Yun-Nung Chen (co-first author)"}}},"pathContext":{"slug":"/posts/humane-typography-in-the-digital-age/"}}

